import { getPaperFillAPI, updatePaperAPI } from '../../api/admin/table'
import {
  getTableAnswerAPI,
  addAnswersAPI,
  submitAnswersAPI,
  reviewAnswersAPI
} from '../../api/user/answer'

const paper = {
  state: {
    paper: {}, /* Paper */
    paperStatistic: {},
    answers: []
  },
  mutations: {
    set_paper(state, paper) {
      state.paper = paper
    },
    set_paperStatistic(state,paperStatistic){
      state.paperStatistic = paperStatistic
    },
    set_answers(state, answers) {
      state.answers = answers
    }
  },
  actions: {
    getFullTable: async({ commit }, tableId) => {
      console.log(`get table ${tableId}`)
      const res = await getTableAnswerAPI(tableId)
      if(res && res.data.code == 0) {
        const table = res.data.data
        console.log(table)
        commit('set_paper', table)
        return true
      } else {
        return false
      }
    },
    getFullTableStatistic: async({commit},tableId) =>{
      console.log(`get paperStatistic with tableId: ${tableId}`)
      const res = await getPaperFillAPI(tableId)
      console.log(res)
      if(res && res.data.success) {
        const paperStatistic = res.data.content
        for(let question of paperStatistic.questionStatistics) {
          if(question.type === 3) {
            continue
          }
          const total = question.filledInNum
          for(let option of question.optionStatistics) {
            option.percent = (Math.floor(option.selectedNum / total * 1000) / 10) + '%'
          }
        }
        console.log(paperStatistic)
        commit('set_paperStatistic',paperStatistic)
        return true
      } else {
        return false
      }
    },
    getAnswersRow: async ({ commit }, tableId) => {
      function parseISODateString(dateString) {  
        const parts = dateString.split('T');  
        const dateParts = parts[0].split('-');  
        const timeParts = parts[1].split(':');  
      
        return new Date(  
            Number(dateParts[0]), // 年份  
            Number(dateParts[1]) - 1, // 月份（注意JavaScript中的月份是从0开始的）  
            Number(dateParts[2]), // 日期  
            Number(timeParts[0]), // 小时  
            Number(timeParts[1]), // 分钟  
            Number(timeParts[2]) // 秒  
        );  
      }  

      console.log(`getAnswerRow with tableId: ${tableId}`)
      const res = await getPaperFillAPI(tableId)
      if(res && res.data.code == 0) {
        const answers = res.data.data
        console.log('get answers')
        for (var index = 0; index < answers.length; index++) {
          let dateString = answers[index].date;
          answers[index].date = parseISODateString(dateString).toLocaleString()
        }
        console.log(answers)
        commit('set_answers', answers)
        return true
      } else {
        return false
      }
    },
    terminateCollection: async (_, paper) => {
      const paperInfo = { ...paper }
      paper.status = paperInfo.status = "STOP"
      const res = await updatePaperAPI(paperInfo)
      // console.log(res)
      console.log(paper)
      return res && res.data.success
    },
    restartCollection: async ({ commit, getters }, date) => {
      const mp = getters.monitorPaper
      let newPaperInfo = {
        ...mp,
      }
      if(date) {
        const transfer = date => `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} 00:00:00`
        const startTime = transfer(date[0])
        const endTime = transfer(date[1])
        mp.startTime = newPaperInfo.startTime = startTime
        mp.endTime = newPaperInfo.endTime = endTime

        const startT = Date.parse(startTime)
        const endT = Date.parse(endTime)
        const current = new Date().valueOf()
        if(endT <= current) {
          mp.status = newPaperInfo.status = "STOP"
        } else if(current >= startT && current <= endT) {
          mp.status = newPaperInfo.status = "START"
        } else {
          mp.status = newPaperInfo.status = "INIT"
        }
      } else {
        mp.startTime = newPaperInfo.startTime = null
        mp.endTime = newPaperInfo.endTime = null
        mp.status = newPaperInfo.status = "START"
      }
      console.log(newPaperInfo)
      const res = await updatePaperAPI(newPaperInfo)
      if(res && res.data.success) {
        commit('set_paperStatistic',newPaperInfo)
        return true
      } else {
        return false
      }
    },
    saveAnswers: async (_, saveForm) => {
      console.log('save answers in actions')
      const res = await addAnswersAPI(saveForm.tableId, saveForm.answers)
      console.log(res)
      return res && res.data.code == 0
    },
    submitAnswers: async (_, submitForm) => {
      console.log('submit answers in actions')
      const res = await submitAnswersAPI(submitForm.tableId, submitForm.data)
      console.log(res)
      return res && res.data.code == 0
    }
  }
}

export default paper