import axios from 'axios'

const api = {
    tableUrl: '/api/admin/table'
}

// 查看用户所有采集表
export function getAdminPapersAPI() {
    return axios({
        url: `${api.tableUrl}`,
        method: 'GET',
    })
}

// 查看完整采集表
export function getTableAPI(tableId) {
    return axios({
        url: `${api.tableUrl}/${tableId}`,
        method: 'GET',
    })
}

// 查看采集表填写概要
export function getPaperFillAPI(tableId) {
    return axios({
        url: `${api.tableUrl}/${tableId}/answer`,
        method: 'GET',
    })
}

// 删除采集表
export function deleteTableAPI(tableId) {
    return axios({
        url: `${api.tableUrl}/${tableId}`,
        method: 'DELETE'
    })
}

// 添加采集表
export function addPaperAPI(data) {
    return axios({
        url: `${api.tableUrl}`,
        method: 'POST',
        data
    })
}

// 修改采集表
export function updatePaperAPI(data) {
    return axios({
        url: `${api.tableUrl}/${data.id}`,
        method: 'PUT',
        data
    })
}

// 修改采集表
export function releaseTableAPI(data) {
    console.log(data)
    return axios({
        url: `${api.tableUrl}/${data.id}/release`,
        method: 'POST',
        data
    })
}

// 删除采集表
export function stopReleaseTableAPI(tableId) {
    return axios({
        url: `${api.tableUrl}/${tableId}/release`,
        method: 'DELETE'
    })
}

