import router from './index'
import store from '../store'
import { getToken } from '../utils/auth'

router.beforeEach((to, from, next) => {
  if(store.state.user.loginState && to.path === '/login') {
    next({ name: 'overview' })
    return
  }

  const token = getToken()
  if(token) {
    store.commit('set_loginState', true)
    store.commit('set_userDetails', { id: Number(token) })
    if(to.path === '/login') {
      next({ name: 'overview' })
    } else {
      next()
    }
    return
  }

  if(to.path === '/login' || to.path.startsWith('/customer')) {
    next()
  } else {
    next('/login')
  }
})