import axios from 'axios'

const api = {
    answerUrl: '/api/user/table'
}

// 查看完整采集表与答案
export function getTableAnswerAPI(tableId) {
    return axios({
        url: `${api.answerUrl}/${tableId}/answer`,
        method: 'GET',
    })
}

// 填写采集表
export function addAnswersAPI(tableId, data) {
    console.log(data)
    return axios({
        url: `${api.answerUrl}/${tableId}/answer`,
        method: 'PUT',
        data: { answers: data }
    })
}

// 提交采集表
export function submitAnswersAPI(tableId, data) {
    console.log(data)
    return axios({
        url: `${api.answerUrl}/${tableId}/answer/submit`,
        method: 'PUT',
        data
    })
}

export function reviewAnswersAPI(tableId) {
    return axios({
        url: `${api.answerUrl}/${tableId}/reviewAnswers`,
        method: 'GET'
    })
}