import {
  getAdminPapersAPI,
  getTableAPI,
  getPaperFillAPI,
  addPaperAPI,
  updatePaperAPI,
  releaseTableAPI,
  deleteTableAPI,
  stopReleaseTableAPI
} from '@/api/admin/table'

import {
  getUserPapersAPI,
} from '@/api/user/table'

import { updateQuestionAPI } from '@/api/admin/question'

const paper = {
  state: {
    paperList: [] /* Paper[] */,
    currentPaper: {
      paperInfo: {} /* Paper */,
      questionList: []
    },
    createPaperVisible: false // show createPaper Component
  },
  mutations: {
    set_tableList(state, paperList) {
      state.paperList = paperList
    },
    setTableId(state, id) {
      state.currentPaper.id = id
    },
    set_paperInfo(state, paperInfo) {
      state.currentPaper.paperInfo = paperInfo
    },
    set_questionList(state, questionList) {
      state.currentPaper.questionList = questionList
    },
    add_question(state, question) {
      state.currentPaper.questionList.push(question)
    },
    set_createPaperVisible(state, bool) {
      state.createPaperVisible = bool
    }
  },
  actions: {
    // 查看管理员所有采集表
    getAdminAllTables: async ({ commit, getters }) => {
      const res = await getAdminPapersAPI()
      if(res && res.data.code == 0) {
        const tableList = res.data.data
        console.log(tableList)
        commit('set_tableList', tableList)
        console.log(getters)
        console.log(getters.allTables)
      }
    },
    // 查看用户所有可填写的采集表
    getUserAllTables: async ({ commit, getters }) => {
      const res = await getUserPapersAPI()
      if(res && res.data.code == 0) {
        const tableList = res.data.data
        console.log(tableList)
        commit('set_tableList', tableList)
        console.log(getters)
        console.log(getters.allTables)
      }
    },
    // 创建采集表
    createNewTable: async ({ commit, getters }, paperParam /* Paper */) => {
      console.log(paperParam)
      const res = await addPaperAPI(paperParam)
      if(!(res && res.data.code == 0)) {
        return false
      }
      const tableId = res.data.data
      const username = getters.userDetails.username;
      let tableInfo = {
        id: tableId,
        title: paperParam.title,
        author: username,
        update_date: Date(),
        questions: [],
        status: "UNPUBILSH"
      }
      commit('setTableId', tableId)
      commit('set_paperInfo', tableInfo)
      commit('set_questionList', [])
      return tableId
    },
    // 继续编辑已有采集表
    editTable: async ({ commit }, tableId) => {
      const res = await getTableAPI(tableId)
      if(res && res.data.code == 0) {
        const table = res.data.data
        commit('set_paperInfo', table)
        commit('set_questionList',table.questions)
        return true
      } else {
        return false
      }
    },
    updateTable: async({ commit }, paperInfo) => {
      const res = await updatePaperAPI(paperInfo)
      if(res && res.data.code == 0) {
        commit('set_paperInfo', paperInfo)
        return {
          success: true,
          message: "保存成功"
        }
      } else {
        return {
          success: false,
          message: res.data.message
        }
      }
    },
    deleteTable: async({ state }, index) => {
      const targetPaper = state.paperList[index]
      const res = await deleteTableAPI(targetPaper.id)
      if(res && res.data.code == 0) {
        state.paperList.splice(index, 1)
        return true
      } else {
        return false
      }
    },
    releaseTable: async({ getters }, releaseForm) => {
      const paperInfo = getters.paperInfo
      if(paperInfo.startTime == null) {
        paperInfo.status = "PUBLISHING"
      }
      console.log(paperInfo)
      console.log(1)
      const res = await releaseTableAPI(releaseForm)
      console.log(res)
      return res
    },
    stopReleaseTable: async({ getters, state }, index) => {
      const paperInfo = getters.paperInfo
      paperInfo.status = "UNPUBLISH"
      const targetPaper = state.paperList[index]
      const res = await stopReleaseTableAPI(targetPaper.id)
      state.paperList[index].status = "UNPUBLISH"
      if(res && res.data.code == 0) {
        console.log(state.paperList)
        return true
      } else {
        return false
      }
    },
    // 创建问题
    createQuestion: ({ commit, getters }, type) => {
      let question = {
        title: [],
        type: type
      }
      switch (type) {
        case 'SINGLE_ANSWER_MULTIPLE_CHOICE':
        case 'MULTIPLE_ANSWER_MULTIPLE_CHOICE':
          question.options = [{}]
          break
        
        case 'FILL_IN':
        case 'FILE_UPLOAD':
        case 'LIST_QUESTION':
        default:
          break;
      }
      let paperInfo = getters.paperInfo
      paperInfo.questions.push(question)
      commit('add_paperInfo', paperInfo)
      console.log(question)
      console.log(paperInfo)
    },
    // 更新问题
    updateQuestion: async (_, ques) => {
      const questionId = ques.id
      const options = ques.options
      for(let index in options) {
        options[index].questionId = questionId
        options[index].sequence = Number(index) + 1
      }
      console.log(ques)
      const res = await updateQuestionAPI(ques)
      return res && res.data.success
    },
    // 删除问题
    deleteQuestion: ({ state }, index) => {
      const questionList = state.currentPaper.questionList
      questionList.splice(index, 1)
    }
  }
}

export default paper
