import { loginAPI, registerAPI, logoutAPI } from '../../api/account/user'
import { setToken, removeToken } from '../../utils/auth'

const user = {
  state: {
    loginState: false,
    userDetails: {}
  },
  mutations: {
    set_loginState(state) {
      state.loginState = true
    },
    set_userDetails(state, userDetails) {
      state.userDetails = userDetails
    }
  },
  actions: {
    // 登入
    login: async ({ commit }, loginParam /* UserParam */) => {
      const loginForm = {
        username: loginParam.username1,
        password: loginParam.password
      }
      const res = await loginAPI(loginForm)
      if (res && res.data && res.data.code == 0) {
        const userDetails = {
          username: loginParam.username1,
          role: res.data.data.role
        }
        console.log(userDetails)
        commit('set_userDetails', userDetails)
        commit('set_loginState', true)
        // 存放Token
        localStorage.setItem('access', res.data.data.access_token)
        localStorage.setItem('refresh', res.data.data.refresh_token)
        setToken(userDetails)
        return {
          success: true,
          message: "登入成功"
        }
      } else {
        console.log(res.data.code);
        return {
          success: false,
          message: res.data.message
        }
      }
    },
    // 注册
    register: async (_, registerParam /* UserParam */) => {
      const registerForm = {
        username: registerParam.newUser,
        password: registerParam.newPW,
        role: registerParam.newRole,
        email: registerParam.email
      }
      const res = await registerAPI(registerForm)
      console.log(res)
      return res.data
    },
    // 登出
    logoutAct: ({ commit }) => {
      commit('set_userDetails', {})
      commit('set_loginState', false)
      removeToken()
      localStorage.removeItem('access')
      localStorage.removeItem('refresh')
      return true
    }
  }
}

export default user
