import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'default',
    redirect: { name: 'login' }
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/Login')
  },
  {
    path: '/customer',
    name: 'customer',
    component: () => import('../views/customer/UserIndex'),
    children: [
      {
        path: 'home',
        name: 'home',
        component: () => import('../views/customer/Home')
      },
    ]
  },
  {
    path: '/customer/table/:tableId',
    name: 'fill',
    component: () => import('../views/customer/Fill')
  },
  {
    path: '/manage',
    name: 'manage',
    component: () => import('../views/editor/Editor'),
    children: [
      {
        path: 'overview',
        name: 'overview',
        component: () => import('../views/editor/overview/Overview')
      },
      {
        path: 'create/:tableId',
        name: 'create',
        component: () => import('../views/editor/create/Create')
      },
      {
        path: 'monitor/:tableId',
        name: 'monitor',
        component: () => import('../views/editor/monitor/Monitor')
      },
      {
        path: 'release/:tableId',
        name: 'release',
        component: () => import('../views/editor/create/Release')
      },
    ]
  },
  {
    path: '/manage/table/:tableId/answer/:answerId',
    name: 'collection',
    component: () => import('../views/editor/Collection')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
