import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import '@/router/permission'

import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)

axios.defaults.baseURL = 'http://localhost:8080'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import { getAccessTokenAPI } from '../src/api/token/token'
import { removeToken } from '../src/utils/auth'

Vue.use(ElementUI)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

import { Message } from 'element-ui'

axios.interceptors.request.use(config => {
  const url = config.url
  // 登录和注册无需认证
  if (!url.includes('/api/account/login', '/api/account/register')) {
    let authorization;
    // 从缓存中获取到token
    // 只有获取accessToken才会用到refreshToken
    if (url == '/api/token/access-token') {
      authorization = localStorage.getItem("refresh");
    }
    else {
      authorization = localStorage.getItem("access");
    }
    // 如果token不为null，那么设置到请求头中
    if (authorization) {
      config.headers['Authorization'] = "Bearer " + authorization;
    }
  }
  return config;
}, error => {
  //失败后抛出错误
  Promise.reject(error);
})

axios.interceptors.response.use(
  (response) => {
    console.log('响应数据：', response);
    return response; // 对于非错误状态码，直接返回响应数据  
  },
  (error) => {
    // 响应拦截器的第二个参数是一个函数，用于处理错误  
    console.error('响应错误：', error);
    if (error.response) {
      // 请求已发出，但服务器响应的状态码不在 2xx 范围内
      var res, url;
      switch (error.response.status) {
        case 401:
          url = error.response.config.url;
          if (url == "/api/token/access-token") {
            Message.error('需要登录');
            removeToken()
            localStorage.removeItem('access')
            localStorage.removeItem('refresh')
            // 重定向至登陆界面
            window.location.href = '/login';
            console.info('重定向至登陆界面');
          }
          else {
            return new Promise((resolve, reject) => {  
              getAccessTokenAPI().then(res => {  
                if (res.data && res.data.data) {  
                  localStorage.setItem('access', res.data.data);  
                  // 使用新的 accessToken 重新发送原始请求  
                  const config = error.response.config;  
                  // 确保将新的 accessToken 添加到 headers 中  
                  config.headers.Authorization = `Bearer ${localStorage.getItem('access')}`;  
                  return axios(config).then(resolve).catch(reject);  
                } else {  
                  // 如果获取新的 accessToken 失败，则拒绝 Promise  
                  reject(error);  
                }  
              }).catch(reject);
            })
          }
          break
        case 404:
          Message.error('资源未找到');
          break;
        case 500:
          Message.error('服务器异常');
          break;
        default:
          Message.error(error.response.data.message || '未知错误');
      }
      // 返回一个 rejected Promise  
      return Promise.reject(error);
    } else if (error.request) {
      // 请求已发出，但没有收到响应  
      console.error('请求已发出，但没有收到响应');
      Message.error('请求失败，请稍后再试');
      // 返回一个 rejected Promise  
      return Promise.reject(error);
    } else {
      // 发送请求时发生了某些事情，导致请求没有发出  
      console.error('发送请求时发生了错误', error.message);
      Message.error('请求失败，请检查网络连接');
      // 返回一个 rejected Promise  
      return Promise.reject(error);
    }
  }
);